nav a{
    font-size: 1rem  !important;
    font-weight: 600;
    margin-right: 5px;
}
nav button {
    background-color: #FF6E40;
    padding: 8px 16px;
    border-radius: 5px;
    border: none;
    color: white;
    transition: all 0.5s;
}
nav button:hover {
    color: black;
}
.brand {
    font-size: 1.8rem;
    font-weight: bold;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.container{
    font-family: 'Montserrat', sans-serif;
}
.form {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.login-form {
    border: 1px solid lightgray;
    width: 350px;
    margin: 20px auto;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}
.login-form h4 {
    font-weight: bold !important;
}
input {
    width: 300px;
    border: none;
    border-bottom: 1px solid lightgray;
    margin-bottom: 20px !important;
    transition: all 0.5s;
}
input:focus {
    outline: none;
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}
.form-inputs {
    margin: 20px auto 10px auto;
}
.create-btn {
    border: none;
    width: 300px;
    background-color: #FF6E40;
    color: white;
    padding-top: 5px;
    padding-bottom: 5px;
    border-radius: 5px;
}
.login-form p {
    font-size: 0.7rem;
}
.login-form p span {
    color: #FF6E40 !important;
    cursor: pointer;

}
.fire-auth {
    margin: 0px auto 50px auto;
}
.google-sign-in {
    margin-bottom: 10px;
}
.google-sign-in , .facebook-sign-in {
    border: 1px solid black;
    width: 300px;
    border-radius: 20px;
    padding-top: 5px;
    padding-bottom: 5px;
    background-color: white;
    font-size: 0.8rem;
} 

.route {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    background-color: #FF6E40;
    width: 185px;
    padding-top: 10px;
    padding-left: 20px;
    color: white;
    border-radius: 10px;
}
.route p {
    font-size: 1rem  !important;
    display: inline;
    margin-bottom: 0;
}
.vehicle-info img {
    width: 25%;
}
.vehicle-info p {
    margin-top: 10px;
}
.vehicle-info .people {
    width: 20%;
}
.vehicle-info {
    width: 185px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    background-color:white;
    padding: 8px 5px;
    border-radius: 10px;
}
.route .line {
    height: 50px;
    width: 1px;
    background-color: white;
    display: inline;
    border-radius: 50%;
}
.search-box {
    background-color: rgb(236, 236, 236);
    /* width: 200px; */
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    padding-top: 10px;
    padding-bottom: 15px;
    border-radius: 10px;
}
.search-box p {
    font-size: 0.8rem;
}
.search-btn {
    border: none;
    background-color: #FF6E40;
    border-radius: 5px;
    color: white;
    padding-top: 5px;
    padding-bottom: 5px;
    font-size: 0.8rem;
}
.pick {
    border: none;
    padding-top: 5px;
    padding-bottom: 5px;
    border-radius: 5px;
    font-size: 0.8rem;
}
.pick:focus {
    outline: none;
}
.search-btn, .pick {
    width: 170px;
}
.map img {
    width: 100%;
}
.map {
    width: 100%; 
    height: 450px;
    border-radius: 10px;
}
@media (max-width: 500px) {
    .map {
        margin-top: 20px;
        margin-bottom: 20px;
    }
}
@media (min-width: 580px) and (max-width: 768px)  {
    .map {
        margin-top: 20px;
        margin-bottom: 20px;
    }
}
